import React, { useState,useEffect,array } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CCollapse,
  CSelect,
  CFade,
  CDropdownMenu,
  CDropdownItem,
  CDropdown,
  CDropdownDivider,
  CDropdownToggle,
  CLabel,
  CSwitch,
  CFormGroup,
  CInput,
  CInputRadio,
  CInputFile,
  CRow 
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { DocsLink } from 'src/reusable'
//import { for } from 'core-js/fn/symbol'
import { useHistory } from 'react-router-dom';



const Create = (empid) => {
  const history = useHistory()
let path= empid.location.pathname;
let param=!path ? "":path.split("/")[4];
if(!param){
  param=0;
}
  const [collapse, setCollapse] = useState(true)
  const [collapseMulti, setCollapseMulti] = useState([false, false])
  const [accordion, setAccordion] = useState(1)
  const [fade, setFade] = useState(true)
  const [page, setPage] = useState(1); 
  const DefEmpdata= {
    FIRST_NAME:"",
   LAST_NAME:"",
   EMAIL_ID:"",
   PK_EMP_ID:parseInt(param),
    IS_ACTIVE:0,
    BRANCHID:0,
    PREFIX_ID :0,
    LANGUAGE_ID :0,
    DEPT_ID :0,
    JOB_TITLE_ID:0,
    MANAGER_ID:0,
    EMPLOYEEID:"",
    PASSWORD:"",
    CPASSWORD:"",
    MOBILE_NO:"",
    TEL_NO: "",
    GENDER:"",
    MARITAL_STATUS:"",
    NATIONALITY:"",
    BLOOD_GROUP:"",
    PERSONAL_EMAIL:"",
    PHOTO:"",
    DATE_OF_JOIN:1/12/2020,
    DATE_OF_BIRTH:1/12/2020,
    ALLOW_LOGIN:0,
    StatusSwitch: false,
    selectedDept:"",
    selectedbranch:"",
    selectedpfix:"",
    selectedlang:"",
    selectedjob:"",
    selectedmnger:""
  
  }
const DefdeptData=[{
   
  departmenT_NAME:"--select Department--",
  pK_DEPARTMENT_ID:0
}]
const DefbranchData=[{
  brancH_NAME:"--select branch--",
  pK_BRANCH_ID:0
}]
const DefpfixData=[{
   
  name:"--select prefix--",
  pK_PREFIX_ID:0
}]
const DeflangData=[{
   
  name:"--select language--",
  pK_LANGUAGE_ID:0
}]
const DefmngerData=[{
   
  NAME:"--select manager--",
  pK_EMP_ID:0
}]
const DefjobData=[{
   
  name:"--select designation--",
  pK_DESIGNATION_ID:0
}]


const [emplist, setempList] = useState([]);
const [branchlist, setbranchList] = useState([]);
const [Deptdata,setdeptdata]=useState(DefdeptData);
const [Branchdata,setbranchdata]=useState(DefbranchData);
const [pfixdata,setpfixdata]=useState(DefpfixData);
const [lngdata,setlngdata]=useState(DeflangData);
const [mngdata,setmngdata]=useState(DefmngerData);
const [jobdata,setjobdata]=useState(DefjobData);
const [Empdata,setempdata]=useState(DefEmpdata);
 //const [isLoading, setIsLoading] = useState(true);
const ChangeSwitch = (event)=>{
  const { name, value } = event.target;
  Empdata.StatusSwitch=event.target.checked;
  Empdata.IS_ACTIVE=event.target.checked == true?1:0;
  setempdata({ ...Empdata, [name]: event.target.checked })
}

useEffect(()=>{
  Loaddropdown();
  if(DefEmpdata.PK_EMP_ID>0){
    fetch("http://localhost:51264/api/EMPLOYEE_MAIN_DETAILS")
    .then(res => res.json())
      .then(response => {
        const emplist=[];
        //setempdata([]);
       // setdeptdata(DefdeptData);
      //  dept_data.push({departmenT_NAME:"--Select Departments--",      pK_DEPARTMENT_ID:""})
        for (const i of response) {
          const data={};
          data["PK_EMP_ID"]=i.pK_EMP_ID;
          data["FIRST_NAME"]=i.firsT_NAME;
          data["LAST_NAME"]=i.lasT_NAME;
          data["PREFIX_ID"]=i.prefiX_ID;
          data["BRANCHID"]=i.branchid;
          data["DEPT_ID"]=i.depT_ID;
          data["MANAGER_ID"]=i.manageR_ID;
          data["JOB_TITLE_ID"]=i.joB_TITLE_ID;
          data["LANGUAGE_ID"]=i.languagE_ID;
          data["EMAIL_ID"]=i.emaiL_ID;
          data["MOBILE_NO"]=i.mobilE_NO;
          data["TEL_NO"]=i.teL_NO;
          data["EMPLOYEEID"]=i.employeeid;
          data["PERSONAL_EMAIL"]=i.personaL_EMAIL;
          data["NATIONALITY"]=i.nationality;
          data["BLOOD_GROUP"]=i.blooD_GROUP;
          data["DATE_OF_JOIN"]=i.datE_OF_JOIN;
          data["DATE_OF_BIRTH"]=i.datE_OF_BIRTH;
          data["GENDER"]=i.gender;
          data["MARITAL_STATUS"]=i.martiaL_STATUS;
          data["PHOTO"]=i.photo;
          data["ALLOW_LOGIN"]=i.alloW_LOGIN;
          data["PASSWORD"]=i.password;
          emplist.push(data)
        }  
        setempList(emplist);
          const obj=emplist.filter((e) => e.PK_EMP_ID == DefEmpdata.PK_EMP_ID)[0];
              Empdata.PK_EMP_ID=obj.PK_EMP_ID;
              Empdata.FIRST_NAME=obj.FIRST_NAME;
              Empdata.LAST_NAME=obj.LAST_NAME;
             // const narray=branchlist.filter((e)=>e.pK_BRANCH_ID ==obj.BRANCHID)[0];
             
              Empdata.PREFIX_ID=obj.PREFIX_ID;
              Empdata.BRANCHID=obj.BRANCHID;
              //dropdownchange(obj.BRANCHID,2);
              
              Empdata.DEPT_ID=obj.DEPT_ID;
              Empdata.MANAGER_ID=obj.MANAGER_ID;
              Empdata.JOB_TITLE_ID=obj.JOB_TITLE_ID;
              Empdata.LANGUAGE_ID=obj.LANGUAGE_ID;
              Empdata.EMAIL_ID=obj.EMAIL_ID;
              Empdata.MOBILE_NO=obj.MOBILE_NO;
              Empdata.TEL_NO=obj.TEL_NO;
              Empdata.EMPLOYEEID=obj.EMPLOYEEID;
              Empdata.PERSONAL_EMAIL=obj.PERSONAL_EMAIL;
              Empdata.NATIONALITY=obj.NATIONALITY;
              Empdata.BLOOD_GROUP=obj.BLOOD_GROUP;
              Empdata.DATE_OF_JOIN=obj.DATE_OF_JOIN;
              Empdata.DATE_OF_BIRTH=obj.DATE_OF_BIRTH;
              Empdata.GENDER=obj.GENDER;
              Empdata.MARITAL_STATUS=obj.MARITAL_STATUS;
              Empdata.PHOTO=obj.PHOTO;
              if(obj.ALLOW_LOGIN==1){
                Empdata.PASSWORD=obj.PASSWORD;
              }
              Empdata.IS_ACTIVE=obj.Status;
              Empdata.StatusSwitch=obj.Status == 1 ?true:false;
              //Empdata.ModelLabel="Update Departments";
              setempdata(Empdata);
        
       // Empdata.selectedDept="";
       
       // setempdata(Empdata);
       // setBranchData(BranchData);
        
      })
   }
}, [page]);
const handleInputChange = (event) => {
  const { name, value } = event.target
  setempdata({ ...Empdata, [name]: value })
}

  const toggle = (e) => {
    setCollapse(!collapse)
    e.preventDefault()
   // setempdata(DefEmpdata);
  }
  const dropdownchange = (event,level) => {
    if(level==1){
      const { name, value } = event.target
    setempdata({ ...Empdata, selectedDept: value })
    }
    else if(level==2){
      const { name, value } = event.target
      setempdata({ ...Empdata, selectedbranch: value })
    
    }
    else if(level==3){
      const { name, value}= event.target
      setempdata({ ...Empdata, selectedpfix: value})
    }
    else if(level==4){
      const { name, value}= event.target
      setempdata({ ...Empdata, selectedlang: value})
    }
    else if(level==5){
      const { name, value}= event.target
      setempdata({ ...Empdata, selectedmnger: value})
    }
    else if(level==6){
      const { name, value}= event.target
      setempdata({ ...Empdata, selectedjob: value})
    }
  }
  const Loaddropdown=()=>{
  
    fetch("http://localhost:51264/api/Departments")
    .then(res => res.json())
      .then(response => {
        const dept_data=[];
        setdeptdata(DefdeptData);
        dept_data.push({departmenT_NAME:"--Select Departments--",      pK_DEPARTMENT_ID:""})
        for (const i of response) {
          const data={};
          data["departmenT_NAME"]=i.departmenT_NAME;
          data["pK_DEPARTMENT_ID"]=i.pK_DEPARTMENT_ID;
          dept_data.push(data)
        }
        Empdata.selectedDept="";
        setdeptdata(dept_data);
        setempdata(Empdata);
       // setBranchData(BranchData);
        
      })
        fetch("http://localhost:51264/api/BUSINESS_UNIT")
        .then(res => res.json())
        .then(response =>{
          const branch_data=[];
          setbranchdata(DefbranchData);
          branch_data.push({brancH_NAME:"--Select branch--", pK_BRANCH_ID:""})
          for (const i of response){
            const data1={};
            data1["brancH_NAME"]=i.brancH_NAME;
            data1["pK_BRANCH_ID"]=i.pK_BRANCH_ID;
            branch_data.push(data1);
            branchlist.push(data1);
          }
          Empdata.selectedbranch="";
          setbranchList(branchlist);
          setbranchdata(branch_data);
          
          setempdata(Empdata);
          //setbranchdata(Branchdata);
          
        
        })
        fetch("http://localhost:51264/api/Common/GetPrefix")
        .then(res => res.json())
        .then(response =>{
          const pfx_data=[];
          setpfixdata(DefpfixData);
          pfx_data.push({name:"--Select prefix--", pK_PREFIX_ID:""})
          for (const i of response){
            const data1={};
            data1["name"]=i.name;
            data1["pK_PREFIX_ID"]=i.pK_PREFIX_ID;
            pfx_data.push(data1)
          }
          Empdata.selectedpfix="";
          setpfixdata(pfx_data);
          setempdata(Empdata);
          //setbranchdata(Branchdata);
          
        
        })
        fetch("http://localhost:51264/api/Common/GetLanguage")
        .then(res => res.json())
        .then(response =>{
          const lng_data=[];
          setlngdata(DeflangData);
          lng_data.push({name:"--Select language--", pK_LANGUAGE_ID:""})
          for (const i of response){
            const data1={};
            data1["name"]=i.name;
            data1["pK_LANGUAGE_ID"]=i.pK_LANGUAGE_ID;
            lng_data.push(data1)
          }
          Empdata.selectedlang=0;
          setlngdata(lng_data);
          setempdata(Empdata);
          //setbranchdata(Branchdata);
          
        
        })
        fetch("http://localhost:51264/api/DESIGNATIONs")
        .then(res => res.json())
        .then(response =>{
          const job_data=[];
          setjobdata(DefjobData);
          job_data.push({name:"--Select designation--", pK_DESIGNATION_ID:""})
          for (const i of response){
            const data1={};
            data1["name"]=i.name;
            data1["pK_DESIGNATION_ID"]=i.pK_DESIGNATION_ID;
            job_data.push(data1)
          }
          Empdata.selectedjob="";
          setjobdata(job_data);
          setempdata(Empdata);
          //setbranchdata(Branchdata);
          
        
        })
        fetch("http://localhost:51264/api/EMPLOYEE_MAIN_DETAILS")
        .then(res => res.json())
        .then(response =>{
          const emp_data=[];
          setmngdata(DefmngerData);
          emp_data.push({NAME:"--Select manager--", pK_EMP_ID:""})
          for (const i of response){
            const data1={};
            data1["NAME"]=i.firsT_NAME +" "+ i.lasT_NAME;
            data1["pK_EMP_ID"]=i.pK_EMP_ID;
            emp_data.push(data1)
          }
          Empdata.selectedmnger="";
          setmngdata(emp_data);
          setempdata(Empdata);
          //setbranchdata(Branchdata);
          
        
        })
        // const CountryDrp={};
        // CountryDrp=Country_Data;
      
      .catch(error => console.log(error));
   
} 
  const toggleMulti = (type) => {
    let newCollapse = collapseMulti.slice()
    switch (type) {
      case "left":
        newCollapse[0] = !collapseMulti[0];
        break;
      case "right":
        newCollapse[1] = !collapseMulti[1];
        break;
      case "both":
        newCollapse[0] = !collapseMulti[0];
        newCollapse[1] = !collapseMulti[1];
        break;
      default:
    }
    setCollapseMulti(newCollapse)
  }
  
  const toggleFade = () => {
    setFade(!fade)
  }
 
  const showpass = (id)=>{
    if(id ==1)
    {
      document.getElementById('passform').style.display ='block';
    }
    else {
      document.getElementById('passform').style.display ='none';
    }
  
  }
  function checkpassword() {
     if(Empdata.PASSWORD!==Empdata.CPASSWORD){
       alert("password missmatch");
       return;
     }

  }
  const Saveemp = ()=>{
    Empdata.ALLOW_LOGIN=parseInt(Empdata.ALLOW_LOGIN);
    Empdata.DEPT_ID=parseInt(Empdata.selectedDept);
    Empdata.BRANCHID=parseInt(Empdata.selectedbranch);
    Empdata.PREFIX_ID=parseInt(Empdata.selectedpfix);
    Empdata.LANGUAGE_ID=parseInt(Empdata.selectedlang);
    Empdata.MANAGER_ID=Empdata.selectedmnger ? parseInt(Empdata.selectedmnger):0;
    Empdata.JOB_TITLE_ID=parseInt(Empdata.selectedjob);
   // Empdata.GENDER=toString(Empdata.GENDER);
    if(Empdata.ALLOW_LOGIN==1){
      checkpassword();
      //return;
    }
    if(!Empdata.FIRST_NAME || !Empdata.LAST_NAME)
    {
      alert("Please fill out the fields");
        return;
    }


  
         //if (CountryData.DEPARTMENT_NAME !="" && CountryData.DEPARTMENT_CODE != null){
      const url="http://localhost:51264/api/EMPLOYEE_MAIN_DETAILS"+(Empdata.PK_EMP_ID > 0 ? "/"+Empdata.PK_EMP_ID+"":"");
      const method=Empdata.PK_EMP_ID > 0 ? "PUT":"POST";
      const requestOptions = {
        method: method,
        headers: { 'Content-Type': 'application/json',
        "Access-Control-Allow-Origin": "*"},
        body: JSON.stringify(Empdata)
    };
    fetch(url, requestOptions)
    .then(response => response.json())
    .then(data => FinishSave(data)
    );
   }

 
  const FinishSave = (result) => {
    if (result > 0) {
      //Loaddropdown();
     // LoadAll();
      alert("Success");
      history.push('/base/company/listing');
     // setModal(!modal);
     
    }
    else if (result =-1 || result == -2){
      alert("insertion failed")
    }
    else {
      alert("Failed");
    }
  }
 
  const Editemp = (id)=>{
    if(id > 0)
    {
    //  const obj=CountryList.filter((e) => e.PK_DEPARTMENT_ID === id)[0];
    //  CountryData.PK_DEPARTMENT_ID=obj.PK_DEPARTMENT_ID;
    //  CountryData.DEPARTMENT_NAME=obj.Department_Name;
    //  CountryData.DEPARTMENT_CODE=obj.Department_Code;
    //  CountryData.IS_ACTIVE=obj.Status;
    //  CountryData.StatusSwitch=obj.Status == 1 ?true:false;
    //  CountryData.ModelLabel="Update Departments";
     // setCountryData(CountryData);
    }
   // setModal(!modal);
  }
  return (
    <CRow>
      <CCol xl="12">
        <CCard>
          <CCardHeader>
            Employee Details
            <DocsLink name="CCollapse"/>
          </CCardHeader>
          <CButton
              color="primary"
              onClick={toggle}
              className={'mb-1'}
            >Main Details</CButton>
          <CCollapse show={collapse}>
            <CCardBody>
                <div className="row">
                   <CCol xs="12" md="4">
                    <CSelect  name="BRANCHID" id="pK_BRANCH_ID" value={Empdata.selectedbranch} onChange={(e)=>dropdownchange(e,2)} >
                       {Branchdata.map(item => (
                        <option
                         key={item.pK_BRANCH_ID}
                         value={item.pK_BRANCH_ID}
                         >
                         {item.brancH_NAME}
                       </option>
                       ))} 
                    </CSelect>
                  </CCol>
              
                  <CCol xs="12" md="4">
                    <CSelect  name="pK_PREFIX_ID" id="pK_PREFIX_ID" value={Empdata.selectedpfix} onChange={(e)=>dropdownchange(e,3)} >
                       {pfixdata.map(item => (
                        <option
                         key={item.pK_PREFIX_ID}
                         value={item.pK_PREFIX_ID}
                         >
                         {item.name}
                       </option>
                       ))} 
                    </CSelect>
                  </CCol>
                  
                  <CCol xs="12" md="4">
                     <CSelect  name="pK_LANGUAGE_ID" id="pK_LANGUAGE_ID" value={Empdata.selectedlang} onChange={(e)=>dropdownchange(e,4)} >
                       {lngdata.map(item => (
                        <option
                         key={item.pK_LANGUAGE_ID}
                         value={item.pK_LANGUAGE_ID}
                         >
                         {item.name}
                       </option>
                       ))} 
                    </CSelect>
                  </CCol>
              </div>
              <br></br>
              <div className="row">
                  <CCol xs="12" md="4">
                  <CSelect  name="pK_DEPARTMENT_ID" id="pK_DEPARTMENT_ID" value={Empdata.selectedDept} onChange={(e)=>dropdownchange(e,1)} >
                    {Deptdata.map(item => (
        <option
          key={item.pK_DEPARTMENT_ID}
          value={item.pK_DEPARTMENT_ID}
        >
          {item.departmenT_NAME}
        </option>
      ))} 
      </CSelect>
      </CCol>
                  <CCol xs="12" md="4">
                  <CSelect  name="pK_DESIGNATION_ID" id="pK_DESIGNATION_ID" value={Empdata.selectedjob} onChange={(e)=>dropdownchange(e,6)} >
                       {jobdata.map(item => (
                        <option
                         key={item.pK_DESIGNATION_ID}
                         value={item.pK_DESIGNATION_ID}
                         >
                         {item.name}
                       </option>
                       ))} 
                    </CSelect>
                 </CCol>
                  <CCol xs="12" md="4">
                     <CSelect  name="pK_EMP_ID" id="pK_EMP_ID" value={Empdata.selectedmnger} onChange={(e)=>dropdownchange(e,5)} >
                       {mngdata.map(item => (
                        <option
                         key={item.pK_EMP_ID}
                         value={item.pK_EMP_ID}
                         >
                         {item.NAME}
                       </option>
                       ))} 
                    </CSelect>
                  </CCol>
               </div>
               <br></br>
              
              <div className="row">
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="fname">First Name</CLabel>
                <CInput id="fname" placeholder="Enter your First name" name="FIRST_NAME" value={Empdata.FIRST_NAME}  onChange={(e)=>handleInputChange(e)} />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="lname">Last Name</CLabel>
                <CInput id="lname" placeholder="Enter your Last name" name="LAST_NAME" value={Empdata.LAST_NAME} onChange={(e)=>handleInputChange(e)}  />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="email">email</CLabel>
                <CInput id="email" placeholder="Enter your email" name="EMAIL_ID" value={Empdata.EMAIL_ID} onChange={(e)=>handleInputChange(e)}  />
              </CFormGroup>
             </div>
             <div className="row">
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="mno">Mobile No</CLabel>
                <CInput id="mno" placeholder="Enter your Mobile no" name="MOBILE_NO" value={Empdata.MOBILE_NO} onChange={(e)=>handleInputChange(e)}  />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="tel">Telephone No</CLabel>
                <CInput id="tel" placeholder="Enter your Telephone no" name="TEL_NO"  value={Empdata.TEL_NO} onChange={(e)=>handleInputChange(e)} />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="empid">Employee Id</CLabel>
                <CInput id="empid" placeholder="Enter your Employee Id" name="EMPLOYEEID" value={Empdata.EMPLOYEEID} onChange={(e)=>handleInputChange(e)}  />
              </CFormGroup>
              </div>
              <div className="row">
              <CFormGroup className="col-xs-12 col-md-4">
                <CLabel htmlFor="Pmail">Personal Email</CLabel>
                <CInput id="Pmail" placeholder="Enter your Personal Email" name="PERSONAL_EMAIL" value={Empdata.PERSONAL_EMAIL}  onChange={(e)=>handleInputChange(e)} />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4" >
                <CLabel htmlFor="Nation">Nationality</CLabel>
                <CInput id="Nation" placeholder="Enter your Nationality" name="NATIONALITY" value={Empdata.NATIONALITY} onChange={(e)=>handleInputChange(e)} />
              </CFormGroup>
              <CFormGroup className="col-xs-12 col-md-4" >
                <CLabel htmlFor="blood">Blood Group</CLabel>
                <CInput id="blood" placeholder="Enter your Blood Group" name="BLOOD_GROUP" value={Empdata.BLOOD_GROUP} onChange={(e)=>handleInputChange(e)} />
              </CFormGroup>
              </div>
            <div className="row">
             
                <CFormGroup  className="col-xs-12 col-md-4">
                    <CLabel htmlFor="doj">Date of join</CLabel>
                    <CInput type="date" id="doj" name="DATE_OF_JOIN" placeholder="date" value={Empdata.DATE_OF_JOIN} onChange={(e)=>handleInputChange(e)}  />        
                </CFormGroup>
                <CFormGroup  className="col-xs-12 col-md-4">
                    <CLabel htmlFor="dob">Date of birth</CLabel>
                    <CInput type="date" id="dob" name="DATE_OF_BIRTH" placeholder="date" value={Empdata.DATE_OF_BIRTH} onChange={(e)=>handleInputChange(e)} />        
                </CFormGroup>
               
            </div>
            <CFormGroup row >
                  <CCol md="3">
                    <CLabel>Gender</CLabel>
                  </CCol>
                  
                  <CCol md="9">
                    <CFormGroup variant="custom-radio" inline value={Empdata.GENDER} onChange={(e)=>handleInputChange(e)} >
                      <CInputRadio custom id="fem" name="GENDER" value="F" />
                      <CLabel variant="custom-checkbox" htmlFor="fem">Female</CLabel>
                    </CFormGroup>
                    <CFormGroup variant="custom-radio" inline value={Empdata.GENDER} onChange={(e)=>handleInputChange(e)}>
                      <CInputRadio custom id="mal" name="GENDER" value="M" />
                      <CLabel variant="custom-checkbox" htmlFor="mal">Male</CLabel>
                    </CFormGroup>
                    <CFormGroup variant="custom-radio" inline value={Empdata.GENDER} onChange={(e)=>handleInputChange(e)}>
                      <CInputRadio custom id="other" name="GENDER" value="O" />
                      <CLabel variant="custom-checkbox" htmlFor="other">other</CLabel>
                    </CFormGroup>
                  </CCol>
                </CFormGroup>
            <CFormGroup row >
                  <CCol md="3">
                    <CLabel>Maritial Status</CLabel>
                  </CCol>
                  
                  <CCol md="9">
                    <CFormGroup variant="custom-radio" inline value={Empdata.MARITAL_STATUS} onChange={(e)=>handleInputChange(e)} >
                      <CInputRadio custom id="inline-radio1" name="MARITAL_STATUS" value="M" />
                      <CLabel variant="custom-checkbox" htmlFor="inline-radio1">Married</CLabel>
                    </CFormGroup>
                    <CFormGroup variant="custom-radio" inline  value={Empdata.MARITAL_STATUS} onChange={(e)=>handleInputChange(e)}>
                      <CInputRadio custom id="inline-radio2" name="MARITAL_STATUS" value="S" />
                      <CLabel variant="custom-checkbox" htmlFor="inline-radio2">Single</CLabel>
                    </CFormGroup>
                    
                  </CCol>
                </CFormGroup>
            <CFormGroup row >
                  <CLabel col md="3" htmlFor="file-input">Photo</CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile id="file-input" name="PHOTO" value={Empdata.PHOTO} onChange={(e)=>handleInputChange(e)} />
                  </CCol>
                </CFormGroup>
                
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel>Status</CLabel>
                  </CCol>
                  <CCol md="9">
                 <CFormGroup>
                    <CSwitch className={'mx-1'} variant={'3d'} name="StatusSwitch" color={'primary'} checked={Empdata.StatusSwitch} onChange={(e)=>ChangeSwitch(e)} />
                  </CFormGroup>
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel>Allow login</CLabel>
                  </CCol>
                  <CCol md="9">
                    <CFormGroup variant="custom-radio" inline value={Empdata.ALLOW_LOGIN} onChange={(e)=>handleInputChange(e)}  >
                      <CInputRadio custom id="login1" name="ALLOW_LOGIN" value="1" onClick={() => showpass(1)} />
                      <CLabel variant="custom-checkbox" htmlFor="login1">yes</CLabel>
                    </CFormGroup>
                    <CFormGroup variant="custom-radio" inline value={Empdata.ALLOW_LOGIN} onChange={(e)=>handleInputChange(e)}>
                      <CInputRadio custom id="login2" name="ALLOW_LOGIN" value="0" onClick={() => showpass(0)} />
                      <CLabel variant="custom-checkbox" htmlFor="login2">No</CLabel>
                    </CFormGroup>
                    
                  </CCol>
                </CFormGroup>
                <CFormGroup row style={{display:'none'}} id="passform" className="col-xs-12 col-md-5">
                <CLabel htmlFor="pass">Password</CLabel>
                <CInput id="pass" placeholder="Enter Password " name="PASSWORD" type="password" value={Empdata.PASSWORD} onChange={(e)=>handleInputChange(e)}  />
                 <br></br>
                  <CLabel htmlFor="cpass">Confirm Password</CLabel>
                <CInput id="cpass" placeholder="Confirm Password " name="CPASSWORD" type="password"  value={Empdata.CPASSWORD}  onChange={(e)=>handleInputChange(e)}  />
              
              </CFormGroup>
              <CFormGroup  style={{float:'right'}} > 
                <CButton type="submit" size="sm" color="primary"onClick={(e)=>Saveemp()}><CIcon name="cil-scrubber" /> Submit</CButton>
                 <CButton type="reset" size="sm" color="danger"><CIcon name="cil-ban" /> Reset</CButton>
              </CFormGroup>
            </CCardBody>
          </CCollapse>
          {/* <CCardFooter>
        
          </CCardFooter> */}
        </CCard>

        
     

        
      </CCol>
      
    </CRow>
  )
}

export default Create
